.recreational {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: #1c1b1a;
    color: white;
}

.header { 
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 500px;
    background-color: #333;
    background-image: url(https://images.unsplash.com/photo-1606092195730-5d7b9af1efc5?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80);
    background-blend-mode: multiply;
    background-attachment: fixed;
    background-size: cover;
    color: white;
    text-align: center;
    padding:  15vh 1em;
}

.header > img {
    height: 600px;
}


h1 {
    font-size: 10vh;
}



.body > p{
    display: block;
    flex: 0.5;
    font-size: 40px;
    padding: 40px;
}


.body2 { 
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: fit-content;
    padding: 10px;
    margin: 20px;
}

.body2 > img {
    border-radius: 30px;
height: 500px;
}

.body2 > p{
    display: block;
    padding: 20px;
    font-size: 40px;

}