.greenland {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: #1c1b1a;
    color: white;
}

h1 {
    font-size: 10vh;
}

.header1 { 
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 500px;
    background-color: #333;
    background-image: url(https://images.unsplash.com/photo-1605211672467-b3a9bcee8a5c?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=632&q=80);
    background-blend-mode: multiply;
    background-attachment: fixed;
    background-size: cover;
    color: white;
    text-align: center;
    padding:  15vh 1em;
}

.header1 > img {
    height: 600px;
}

.header1 > h1 {
    font-size: 50px;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    margin: 0%;
    padding: 0;
}