
.header9 { 
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 500px;
    background-color: #333;
    background-image: url(https://i.pinimg.com/originals/55/c2/71/55c2715a53682c4c8f3e29fa66cf47e5.jpg);
    background-blend-mode: multiply;
    background-attachment: fixed;
    background-size: cover;
    color: white;
    text-align: center;
    padding:  15vh 1em;
}

.body {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: fit-content;
    padding: 10px;
    margin: 20px;
}

.body > img {
    height: 550px;
    border-radius: 30px;
}

.body > p{
    display: block;
    font-size: 30px;
    padding: 8px;
}

.body2 { 
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: fit-content;
    padding: 10px;
    margin: 20px;
}

.body2 > img {
    border-radius: 30px;
height: 500px;
}

.body2 > ul > li{
    padding: 20px;
    font-size: 30px;

}