.recreational {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: #1c1b1a;
  color: white;
}

.header0 {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 500px;
  background-color: #333;
  background-image: url(https://b2bm.s3.amazonaws.com/styles/default_image/s3/istock-607925210_edited.jpg?itok=PaCTPALF);
  background-blend-mode: multiply;
  background-attachment: fixed;
  background-size: cover;
  color: white;
  text-align: center;
  padding: 15vh 1em;
}

.header > img {
  height: 600px;
}

h1 {
  font-size: 10vh;
}

.body > p {
  display: block;
  flex: 0.5;
  font-size: 40px;
  padding: 40px;
}

.body2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: fit-content;
  padding: 10px;
  margin: 20px;
}

.body2 > img {
  border-radius: 30px;
  height: 500px;
}

.body2 > p {
  display: block;
  padding: 20px;
  font-size: 40px;
}

.body1 {
  display: flex;
  align-items: center;
}
.body1 > p {
  font-size: 40px;
  padding: 40px;
}

.body1 > img {
  border-radius: 30px;
  height: 500px;
}
