.restaurant {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: #1c1b1a;
    color: white;
}

h1 {
    font-size: 10vh;
}
.header4 { 
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 500px;
    background-color: #333;
    background-image: url(https://images.unsplash.com/photo-1504754524776-8f4f37790ca0?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80);
    background-blend-mode: multiply;
    background-attachment: fixed;
    background-size: cover;
    color: white;
    text-align: center;
    padding:  15vh 1em;
}


.header4 > img {
    height: 650px;
    border-radius: 20px;
    padding: 0;
}


.body {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: fit-content;
    padding: 10px;
    margin: 20px;
}

.body > img {
    height: 550px;
    border-radius: 30px;
}

.body > p{
    display: block;
    font-size: 45px;
    padding: 8px;
}
