.organic {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: #1c1b1a;
    color: white;
}

h1 {
    font-size: 10vh;
}

.header3 { 
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 500px;
    background-color: #333;
    background-image: url(https://images.unsplash.com/photo-1556386734-4227a180d19e?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=649&q=80);
    background-blend-mode: multiply;
    background-attachment: fixed;
    background-size: cover;
    color: white;
    text-align: center;
    padding:  15vh 1em;
}

.header3 > img {
    height: 600px;
}

.header3 > h1 {
    font-size: 50px;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    margin: 0%;
    padding: 0;
}

.body {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: fit-content;
    padding: 10px;
    margin: 20px;
}

.body > img {
    height: 550px;
    border-radius: 30px;
    flex: 0.7;
}

.body > p{
    display: block;
    flex: 0.5;
    font-size: 20px;
    padding: 8px;
}

