.community{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: #1c1b1a;
    color: white;
}

.header2 { 
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 500px;
    background-color: #333;
    background-image: url(http://plewmanarchitects.co.za/images/portfolio/tengile/tengile-09.jpg);
    background-blend-mode: multiply;
    background-attachment: fixed;
    background-size: cover;
    color: white;
    text-align: center;
    padding:  15vh 1em;
}

.header2 > h1 {
    font-size: 70px;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    margin: 0%;
    padding: 0
}


.body {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: fit-content;
    padding: 10px;
    margin: 20px;
}

.body > img {
    width: 550px;
    border-radius: 30px;
    flex: 0.7;
}

.body > p{
    display: block;
    flex: 0.5;
    font-size: 20px;
    padding: 8px;
}

.footer {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 300px;
    background-color: #333;
    background-image: url(http://plewmanarchitects.co.za/images/portfolio/tengile/tengile-09.jpg);
    background-blend-mode: multiply;
    background-attachment: fixed;
    background-size: cover;
    color: white;
    text-align: center;
    padding:  15vh 1em;
    font-size: 55px;
}